.productsCard {
  width: 30%;
  height: 28vw;
  margin-bottom: 50px;
  scale: 1;
  transition: 0.5s;
  .productImage {
    position: relative;
    .discount {
      position: absolute;
      top: 16px;
      left: 16px;
      font-weight: 400;
      font-size: 12px;
      line-height: 24px;
      text-align: center;
      color: #FFFFFF;
      width: 46px;
      height: 24px;
      background: #A18A68;
      border-radius: 4px;
    }
    .outStock {
      position: absolute;
      top: 16px;
      left: 16px;
      font-weight: 400;
      font-size: 12px;
      line-height: 24px;
      text-align: center;
      color: #FFFFFF;
      width: 63px;
      height: 24px;
      background: #A18A68;
      border-radius: 4px;
    }
    img {
      width: 100%;
      border-radius: 8px;
    }
    button {
      width: 100%;
      height: 65px;
      background: rgba(255, 255, 255, 0.5);
      border-radius: 4px 4px 0 0;
      border: none;
      font-weight: 700;
      font-size: 16px;
      line-height: 21px;
      color: #000000;
      visibility: hidden;
      position: relative;
      bottom: 65px;
      transition: 0.1s;
    }
    .soldOutBtn {
      color: gray;
    }
  }
  .productImage:hover button{
    cursor: pointer;
    visibility: visible;
    height: 65px;
    position: relative;
    bottom: 65px;
    transition: 2s;
   }
  h4 {
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    margin-top: 0;
    margin-bottom: 16px;
    position: relative;
    bottom: 40px;
  }
  p {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    color: #A18A68;
    position: relative;
    bottom: 40px;
    margin: 0;
  }
}
.productsCard:hover {
  scale: 1.05;
  transition: 0.5s;
}